.sby-channel-item:nth-child(even) {
  background-color: #f3f4f5;
}

.sby-channel-title {
  font-weight: 600;
  font-size: 14px;
  text-decoration-line: none !important;
  color: #0068a0 !important;
}

.sby-channel-title:hover {
  cursor: pointer;
}

.sby-pagination-button {
  display: inline-block;
  vertical-align: baseline;
  width: 32px;
  height: 32px;
  margin: 0 5px;
  font-size: 20px;
  line-height: 30px;
  text-align: center;
  border: 1px solid #dcdde1;
  border-radius: 2px;
  color: #141b38;
  font-weight: 600;
}

.sby-pagination-button:hover {
  cursor: pointer;
  color: #141b38;
}

.sby-pagination-button.disabled {
  background-color: #e8e8eb;
  color: #8c8f9a;
}

.sby-action-btn {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 7px;
  position: static;
  width: 36px;
  height: 32px;
  left: 0;
  top: 0;
  box-sizing: border-box;
  border-radius: 2px;
  background: #ffffff;
  border: 1px solid #d8dadd;
  flex: none;
  order: 0;
  flex-grow: 0;
  margin: 0 4px;
  transition: all 0.3s cubic-bezier(0.23, 1, 0.32, 1);
}

.sby-action-btn:hover {
  background: #0096cc;
  border: 1px solid #0096cc;
  cursor: pointer;
}

.sby-action-btn:hover svg path {
  fill: #fff;
}

.sby-action-btn-red {
  border: 1px solid #fbd5d5;
}

.sby-action-btn-red:hover {
  background: #d72c2c;
  border: 1px solid #d72c2c;
}

.sby-tooltip {
  background: #2c324c !important;
  border-radius: 2px !important;
  font-weight: 400 !important;
  font-size: 14px !important;
  opacity: 1 !important;
  box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.25);
  padding: 2px 6px !important;
  max-width: 400px;
}

.sby-tooltip a {
  color: #ffffff;
  font-weight: 600;
}

.sby-importer-btn path {
  fill: #fff;
}

.sby-importer-btn svg {
  margin-top: 10px;
}

.sby-importer-popup h1 {
  font-weight: 600;
  font-size: 24px;
  line-height: 120%;
  letter-spacing: -0.01em;
  color: #141b38;
}

.sby-importer-popup p {
  font-weight: 400;
  font-size: 14px;
  line-height: 160%;
}

.sby-importer-input {
  background: #ffffff;
  border: 1px solid #d0d1d7 !important;
  box-sizing: border-box !important;
  border-radius: 1px !important;
  padding: 8px 12px !important;
  height: 38px !important;
}

.sb-btn-sm {
  padding: 9px 15px;
}

.sby-single-videos-header {
  margin-top: 23px !important;
}
.sby-single-videos-header h1 {
  line-height: 0 !important;
}

.sby-single-videos-header-children .sb-btn-orange {
  border: none;
  border-radius: 2px;
  height: 32px;
  cursor: pointer;
}

.sby-single-videos-header-children .sb-btn-grey {
  text-decoration: none;
  height: 30px;
}

.sby-single-videos-header-children .sb-btn {
  padding: 0 12px;
}

.sby-single-videos-header-children {
  padding-top: 9px;
}

.sby-single-video-table-head {
  border-bottom: 1px solid #dcdde1 !important;
}

.sby-single-video-table-foot {
  border-top: 1px solid #dcdde1 !important;
}

.sby-importer-label {
  padding-bottom: 3px;
}

.sby-importer-popup .sb-btn-blue {
  border: none;
  border-radius: 2px;
  height: 38px;
}

.sby-importer-popup .sb-btn-blue.importing {
  background-color: #434960 !important;
}

.sbc-fb-popup-inside {
  border-radius: 4px !important;
}

.sb-btn-blue {
  cursor: pointer;
}

.sb-btn-orange:disabled {
  cursor: not-allowed !important;
}

.sby-tooltip.place-top::before {
  z-index: -1 !important;
}