/*** 1.0 - Global ***/
.clearfix {
  display: inline-block;
}
/* start commented backslash hack \*/
* html .clearfix {
  height: 1%;
}
.clearfix {
  display: block;
}
#sby-settings {
  -webkit-font-smoothing: antialiased;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif;
}
#wpcontent {
  padding-left: 0px;
}
.clearfix:after {
  visibility: hidden;
  display: block;
  font-size: 0;
  content: " ";
  clear: both;
  height: 0;
}

.checkmark {
  width: 21px;
  height: 22px;
  border-radius: 50%;
  display: block;
  stroke-width: 2;
  stroke: #59ab46;
  stroke-miterlimit: 10;
  stroke-dashoffset: 0;
}
.sby-fb-full-wrapper {
  padding: 0 53px;
  padding-top: 82px;
}
.sby-fb-fs {
  width: 100%;
  position: relative;
  float: left;
  box-sizing: border-box;
}
#adminmenu a[href="admin.php?page=sby-support"] {
  display: none;
}
/*** 2.0 - sby CSS Framework ***/
.d-flex {
  display: flex;
}
.justify-between {
  justify-content: space-between;
}
.items-center {
  align-items: center;
}
/* sby Form Fields */
.sb-form-field {
  display: block;
  position: relative;
}
.sb-form-field .help-text {
  font-weight: 400;
  font-size: 14px;
  line-height: 160%;
  color: #434960;
  word-spacing: 0.3px;
}
.sb-form-field .help-text-green {
  color: #59ab46;
}
.sb-form-field .help-text a {
  color: inherit;
  font-weight: 500;
}
/* input field styles */
.sb-form-field .sby-form-field {
  background: #ffffff;
  border: 1px solid #d0d1d7;
  box-sizing: border-box;
  border-radius: 1px;
  height: 38px;
  padding: 8px;
}
.sb-form-field .sby-form-field:focus {
  outline: none;
  box-shadow: none;
}
.sb-form-field .field-icon {
  position: absolute;
  right: 8px;
  top: 10px;
  font-size: 20px;
}
/* sby-checkbox styles */
.sby-checkbox {
  align-items: center;
  border-radius: 100px;
  display: flex;
  font-weight: 700;
  margin-bottom: 15px;
}
.sby-checkbox input[type="checkbox"] {
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  height: 1px;
  overflow: hidden;
  position: absolute;
  white-space: nowrap;
  width: 1px;
}
.toggle-track {
  width: 36px;
  height: 20px;
  position: relative;
  background: #d0d1d7;
  border-radius: 31px;
}
.toggle-indicator {
  width: 16px;
  height: 16px;
  background-color: #fff;
  border-radius: 100px;
  top: 2px;
  position: absolute;
  left: 2px;
  box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.25);
  transition: all 0.3s cubic-bezier(0.23, 1, 0.32, 1);
}
.sby-checkbox input[type="checkbox"]:checked + .toggle-track .toggle-indicator {
  left: 18px;
}
.sby-checkbox input[type="checkbox"]:checked + .toggle-track {
  background: #0096cc;
}

.sby-error-text {
  color: #d72c2c;
}
.sby-error-text a {
  color: inherit;
}
.sby-fb-cp-clpboard {
  width: 0px;
  height: 0px;
  position: absolute;
  left: -100000px;
}
/* sby-select */
.sb-form-field .sby-select {
  min-width: 235px;
  border: 1px solid #d0d1d7;
  padding: 8px 35px 8px 15px;
  height: 50px;
  font-size: 16px;
  color: #141b38;
  -webkit-appearance: none;
  appearance: none;
  background: #fff
    url("data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAoAAAAFCAYAAAB8ZH1oAAAACXBIWXMAAAsTAAALEwEAmpwYAAAAAXNSR0IArs4c6QAAAARnQU1BAACxjwv8YQUAAABSSURBVHgBfc4hCkAhDAbgjV3slQcvvHuYDWY1G8zewyIWb6YuCCLqD4Pt5wtD54NBQA2XVKiWcorl/X7s+DkhJYUhPk54IN5plCue0Tb8M8/aNx/uIpRVqbbFAAAAAElFTkSuQmCC")
    no-repeat right 15px top 55%;
  box-sizing: border-box;
}
.sb-form-field .sby-select.size-md {
  width: 422px;
}
.sb-form-field .sby-select.size-sm {
  width: 210px;
  min-width: 210px;
}
.sb-form-field .sby-select.size-xs {
  min-width: 100px;
  width: 100px;
}
.sb-form-field .sby-textarea {
  width: 100%;
  min-height: 100px;
  padding: 12px;
  border: 1px solid #d0d1d7;
  font-size: 16px;
}
.sb-form-field .sby-textarea::placeholder {
  color: #8c8f9a;
}
.sb-form-field .sby-textarea:focus {
  outline: none;
  box-shadow: none;
  border-color: #9c9ca0;
}
/* sby Buttons */
.sby-btn {
  display: flex;
  align-items: center;
  vertical-align: middle;
  background: #f3f4f5;
  border: 1px solid #dcdde1;
  box-sizing: border-box;
  border-radius: 2px;
  padding: 7px 20px;
  font-weight: 600;
  font-size: 14px;
  line-height: 22px;
  color: #141b38;
  letter-spacing: 0.2px;
  cursor: pointer;
  transition: all 0.15s ease-in-out;
}
.sby-btn:hover {
  background: #ffffff;
  border: 1px solid #d0d1d7;
}
.sby-btn:focus,
.sby-btn:active {
  background: #e8e8eb;
  border: 1px solid #d0d1d7;
}
.sby-btn .sb-btn-icon {
  margin-right: 10px;
  max-width: 15px;
}
.sby-btn.sb-btn-lg {
  height: 50px;
  font-size: 16px;
  padding: 7px 25px;
}
.sby-btn.sb-btn-blue {
  background-color: #0068a0;
  color: #fff;
  border-color: #096292;
}
.sby-btn span {
  line-height: 1;
  margin-right: 5px;
}
.sb-btn-orange {
  background: #fe544f;
  color: #fff;
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 160%;
}
.sb-tabs-container .sb-tabs .sb-btn-orange {
  margin-top: -20px;
}
.sby-btn svg.checkmark {
  stroke: #fff;
  transform: translate(-7px, -3px);
}
.input-hidden {
  height: 0px;
  width: 0px;
  overflow: hidden;
}

