/*** 2.1 Notification Element ***/
.sb-notification-ctn {
  position: fixed;
  bottom: -100px;
  left: 200px;
  z-index: 99999;
  background: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  border-left: 3px solid #fff;
  line-height: 1em;
  padding: 10px 20px;
  padding-left: 0px;
  border-radius: 4px;
  box-shadow: 0px 26.7377px 77.2886px rgba(0, 0, 0, 0.107828),
    0px 14.2952px 41.3222px rgba(0, 0, 0, 0.0894161),
    0px 8.01379px 23.1649px rgba(0, 0, 0, 0.075),
    0px 4.25607px 12.3027px rgba(0, 0, 0, 0.0605839),
    0px 1.77104px 5.11942px rgba(0, 0, 0, 0.0421718);
}
.sb-notification-ctn[data-active="hidden"] {
  -webkit-animation: sby-notification-hide 0.5s forwards linear;
  animation: sby-notification-hide 0.5s forwards linear;
}
.sb-notification-ctn[data-active="shown"] {
  -webkit-animation: sby-notification-show 0.5s forwards linear;
  animation: sby-notification-show 0.5s forwards linear;
}
@-webkit-keyframes sby-notification-show {
  0% {
    bottom: -100px;
  }
  50% {
    bottom: 70px;
  }
  70% {
    bottom: 60px;
  }
  85% {
    bottom: 65px;
  }
  100% {
    bottom: 50px;
  }
}
@keyframes sby-notification-show {
  0% {
    bottom: -100px;
  }
  50% {
    bottom: 70px;
  }
  70% {
    bottom: 60px;
  }
  85% {
    bottom: 65px;
  }
  100% {
    bottom: 50px;
  }
}

@-webkit-keyframes sby-notification-hide {
  0% {
    bottom: 50px;
  }
  55% {
    bottom: 65px;
  }
  70% {
    bottom: 60px;
  }
  85% {
    bottom: 70px;
  }
  100% {
    bottom: -100px;
  }
}
@keyframes sby-notification-hide {
  0% {
    bottom: 50px;
  }
  55% {
    bottom: 65px;
  }
  70% {
    bottom: 60px;
  }
  85% {
    bottom: 70px;
  }
  100% {
    bottom: -100px;
  }
}

.sb-notification-ctn[data-notificationtype="success"] {
  border-color: #59ab46;
}
.sb-notification-ctn[data-notificationtype="error"] {
  border-color: #d72c2c;
}
.sb-notification-ctn[data-notificationtype="message"] {
  border-color: #141b38;
}
.sb-notification-icon {
  width: 25px;
  height: 25px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 10px;
  margin-right: 15px;
}
.sb-notification-icon svg {
  width: 22px;
  height: 22px;
  float: left;
  fill: currentColor;
}

.sb-notification-ctn[data-notificationtype="success"] .sb-notification-icon {
  color: #59ab46;
}
.sb-notification-ctn[data-notificationtype="error"] .sb-notification-icon {
  color: #d72c2c;
}
.sb-notification-ctn[data-notificationtype="message"] .sb-notification-icon {
  color: #141b38;
}

.sb-notification-ctn span {
  font-size: 14px;
  color: #141b38;
  font-weight: 500;
}

