/*** 4.0 - Content ***/

/*** 4.1 - Tab Styles ***/
.sb-tabs-container {
  position: relative;
  width: 100%;
  margin-top: 1px;
}
.sb-tabs-container .sb-tab-content .sb-tab-content-inner {
  width: 100%;
  margin-top: 20px;
  height: auto;
  padding-bottom: 30px;
}
.sb-tabs-container .sby-save-button {
  display: flex;
  justify-content: flex-end;
  margin-top: 30px;
}
.sb-tabs-container .sb-tabs {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 20px;
}
.sb-tabs-container .sb-tabs:after {
  position: absolute;
  content: "";
  width: 100%;
  height: 1px;
  background-color: #dcdde1;
  bottom: 0px;
  left: 0px;
  z-index: -1;
}
.sb-tabs-container .sb-tabs .tab {
  position: relative;
  padding: 9px 25px 15px 25px;
  display: inline-block;
  font-weight: 600;
  font-size: 16px;
  line-height: 160%;
  color: #8c8f9a;
  text-decoration: none;
  cursor: pointer;
  -webkit-transition: linear 0.2s;
  -ms-transition: linear 0.2s;
  transition: linear 0.2s;
}
.sb-tabs-container .sb-tabs .tab:not(:last-child) {
  margin-right: 20px;
}

.sb-tabs-container .sb-tabs .tab:hover {
  color: #2c324c;
}

.sb-tabs-container .sb-tabs .tab:active,
.sb-tabs-container .sb-tabs .tab:focus {
  outline: 0;
  border: none;
  box-shadow: none;
  color: #2c324c;
}

.sb-tabs-container .sb-tabs .tab.active {
  color: #0068a0;
}
.sb-tabs-container .sb-tabs .tab.active:after {
  opacity: 1;
  width: 100%;
}

.sb-tabs-container .sb-tabs .tab:after {
  content: "";
  display: block;
  background-color: #0068a0;
  width: 150%;
  height: 2px;
  bottom: 0;
  left: 0;
  position: absolute;
  opacity: 0;
  transition: all 0.3s cubic-bezier(0.22, 0.51, 0.53, 0.88);
}

.slide-fade-enter-active {
  transition: all 0.3s ease;
  position: absolute;
}
.slide-fade-leave-active {
  position: absolute;
  transition: all 0.3s cubic-bezier(1, 0.5, 0.8, 1);
}
.slide-fade-enter,
.slide-fade-leave-to {
  transform: translateX(10px);
  opacity: 0;
}

