*/

/*** 1.0 - GLOBAL ***/
.clearfix {
  display: inline-block;
}
/* start commented backslash hack \*/
* html .clearfix {
  height: 1%;
}
.clearfix {
  display: block;
}
#sby-support {
  -webkit-font-smoothing: antialiased;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif;
}
#wpcontent {
  padding-left: 0px;
}
#wpbody-content {
  padding-bottom: 40px;
}
.clearfix:after {
  visibility: hidden;
  display: block;
  font-size: 0;
  content: " ";
  clear: both;
  height: 0;
}
/*orange*/
.sb-btn-orange {
  background: #fe544f !important;
  color: #fff !important;
}
.sb-btn-orange:hover {
  background: #ec352f !important;
  color: #fff !important;
}
.sb-btn-orange:focus,
.sb-btn-orange:active {
  background: #bc120e !important;
  color: #fff !important;
}
.sby-fb-cp-clpboard {
  width: 0px;
  height: 0px;
  position: absolute;
  left: -100000px;
}
.sby-fb-full-wrapper {
  padding: 0 53px;
  padding-top: 82px;
}
.sby-fb-fs {
  width: 100%;
  position: relative;
  float: left;
  box-sizing: border-box;
}
#adminmenu a[href="admin.php?page=sby-support"] {
  display: none;
}

.sb-btn-grey:not(:disabled) {
  background: #f3f4f5 !important;
  color: #141b38 !important;
  border: 1px solid #d0d1d7 !important;
}
.sb-btn-grey:not(:disabled):hover {
  background: #fff !important;
  color: #141b38 !important;
  border: 1px solid #dcdde1 !important;
}
.sb-btn-grey:not(:disabled):focus,
.sb-btn-grey:not(:disabled):active {
  background: #e8e8eb !important;
  color: #141b38 !important;
  border: 1px solid #d0d1d7 !important;
}

/*** 2.0 - HEADER ***/
.sby-fb-create-ctn {
  margin-top: 90px;
}
.sby-fb-header {
  height: 64px;
  position: absolute;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  background: #fff;
  padding: 0px 52px;
  z-index: 2;
}
.sby-fb-header-left {
  display: flex;
}
.sby-fb-header-left .sb-social-wall-link-wrap {
  margin-left: 30px;
}
.sb-social-wall-link-wrap {
  display: flex;
  font-size: 14px;
  margin: 10px 0 10px 30px;
}
.sb-social-wall-link:first-child {
  padding-left: 0;
  border-right: 1px solid #ccc;
  color: #0068a0 !important;
  line-height: 1;
}
.sb-social-wall-link {
  padding: 0 12px;
  border-right: 1px solid #ccc;
  color: #0068a0 !important;
  line-height: 1;
}
.sb-social-wall-link a {
  text-decoration: none;
}
.sb-social-wall-link a:focus {
  outline: none;
  box-shadow: none;
}
.sb-social-wall-link:last-child {
  border-right: none;
}
.sby-fb-hd-logo {
  display: flex;
  vertical-align: middle;
  align-items: center;
  gap: 5px;
}
.sby-fb-hd-logo .sb-logo-letters-wrap {
  transform: translate(0px, -2px);
}
.sby-fb-hd-logo .breadcrumb-title {
  font-size: 14px;
  font-weight: 400;
  line-height: 22px;
  letter-spacing: 0em;
  margin-left: 4px;
}
.sby-fb-hd-logo .separator {
  margin: 0 5px 0 10px;
}
.sby-fb-hd-btn {
  height: 38px;
  cursor: pointer;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 0px 15px 0px 16px;
  font-weight: 600;
  font-size: 14px;
  color: #353a41;
  background: #f3f4f5;
  border-radius: 2px;
  border: 1px solid #dcdde1;
  position: relative;
  text-decoration: none;
  transition: all 0.15s ease-in-out;
}
.sby-fb-hd-btn:focus {
  outline: none;
  box-shadow: none;
}
.sby-fb-hd-btn:hover {
  color: inherit;
  background-color: #fff;
}
.sby-fb-hd-btn i {
  margin: 0px 5px;
}
.sby-fb-hd-btn[data-icon="left"] {
  padding-right: 20px !important;
}
.sby-fb-full-wrapper .section-header h1 {
  font-size: 32px;
  line-height: 40px;
}
/*** 3.0 - SUPPORT CONTAINER ***/
.sby-sb-container {
  max-width: 885px;
  position: relative;
  margin: auto;
  margin-top: 33px;
  box-sizing: border-box;
}

/*** 3.1 - SECTION HEADER ***/
.sby-section-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.sby-section-header h2 {
  font-weight: 600;
  font-size: 32px;
  line-height: 40px;
  color: #141b38;
  margin: 0;
}
.sby-section-header .sby-search-doc .sby-search-doc-field {
  position: relative;
  background: #fff;
  border: 1px solid #dcdde1;
  min-width: 283px;
  box-sizing: border-box;
  height: 38px;
  padding: 0px 14px 0px 15px;
  font-weight: 600;
  font-size: 14px;
  line-height: 22px;
  color: #141b38;
  text-decoration: none;
  margin-right: 1px;
}
.sby-section-header .sby-search-doc .sby-search-doc-field .sb-btn-input {
  height: 100%;
  border: none;
  outline: none;
  background-color: transparent;
  margin-left: 25px;
  color: #141b38;
  min-width: 200px;
  transform: translateY(-1px);
}
.sby-section-header
  .sby-search-doc
  .sby-search-doc-field
  .sb-btn-input::placeholder {
  color: #141b38;
}
.sby-section-header .sby-search-doc .sby-search-doc-field:focus,
.sby-section-header .sby-search-doc .sby-search-doc-field .sb-btn-input:focus {
  outline: none;
  box-shadow: none;
}
.sby-section-header .sby-search-doc .sby-search-doc-field .sb-btn-icon {
  position: absolute;
  left: 15px;
  top: 11px;
  cursor: pointer;
}
.sby-section-header .sby-search-doc .sby-search-doc-field .sb-btn-link-icon {
  cursor: pointer;
  position: absolute;
  right: 2px;
  top: 0;
  width: 35px;
  text-align: center;
  height: 100%;
  padding-top: 9px;
  box-sizing: border-box;
}
.sby-section-header .sby-search-doc a .sb-btn-link-icon {
  margin-left: 56px;
}
/*** 3.2 - SUPPORT BLOCK ***/
.sby-support-blocks {
  display: flex;
}
.sby-support-blocks .sby-support-block {
  padding: 22px 18px;
  background: #ffffff;
  box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.05), 0px 4px 5px rgba(0, 0, 0, 0.05);
  border-radius: 2px;
  width: calc(33% - 7px);
  box-sizing: border-box;
  position: relative;
}
.sby-support-blocks .sby-support-block:not(:last-child) {
  margin-right: 14px;
}
.sby-support-blocks .sby-support-block h3 {
  font-style: normal;
  font-weight: 600;
  font-size: 18px;
  line-height: 25px;
  color: #141b38;
  margin-top: 15px;
  margin-bottom: 0;
}
.sby-support-blocks .sby-support-block p {
  font-size: 12px;
  line-height: 18px;
  color: #64748b;
  margin-top: 5px;
}
.sb-block-header img {
  width: 56px;
  height: 56px;
}
.sb-articles-list {
  margin-top: 31px;
  margin-bottom: 79px;
}

.sb-articles-list ul li {
  position: relative;
  margin: 0px;
  padding-right: 10px;
}
.sb-articles-list ul li:not(:last-child) {
  border-bottom: 1px solid #e8e8eb;
  position: relative;
}
.sb-articles-list ul li a {
  font-size: 14px;
  line-height: 22px;
  color: #141b38;
  text-decoration: none;
  display: block;
  padding: 13px 0;
}
.sb-articles-list ul li:not(:last-child):after {
  content: "";
  position: absolute;
  left: 0px;
  bottom: -1px;
  background-color: #0068a0;
  height: 1px;
  width: 0;
  transition: all 0.25s ease-in-out;
}
.sb-articles-list ul li:not(:last-child):hover:after {
  width: 100%;
}
.sb-articles-list ul li a:hover {
  color: #0068a0;
}
.sb-articles-list ul li .sb-list-icon {
  position: absolute;
  right: 0;
  top: calc(50% - 9px);
}
.sb-articles-list ul li .sb-list-icon svg {
  width: 5px;
}
.sb-articles-list ul li .sb-list-icon svg path {
  fill: #8c8f9a;
}
.sby-support-blocks .sby-sb-button {
  margin-top: 50px;
  position: absolute;
  left: 0;
  bottom: 20px;
  width: calc(100% - 40px);
  padding: 0 20px;
}
.sby-support-blocks .sby-sb-button .sb-btn-icon {
  margin-left: 8px;
}
.sby-support-blocks .sby-sb-button .sb-btn-icon svg {
  width: 5px;
  transform: translateY(0px);
  margin-left: 3px;
}
.sby-support-blocks .sby-sb-button a {
  background: #f3f4f5;
  border: 1px solid #dcdde1;
  box-sizing: border-box;
  border-radius: 2px;
  font-weight: 600;
  font-size: 12px;
  line-height: 19px;
  color: #141b38;
  display: block;
  text-align: center;
  text-decoration: none;
  padding: 6px;
  transition: all 0.15s ease-in-out;
}
.sby-support-blocks .sby-sb-button a:hover,
.sby-section-header .sby-search-doc a:hover {
  background: #ffffff;
  border: 1px solid #d0d1d7;
}
.sby-support-blocks .sby-sb-button a:focus,
.sby-support-blocks .sby-sb-button a:active,
.sby-section-header .sby-search-doc a:focus,
.sby-section-header .sby-search-doc a:active {
  background: #e8e8eb;
  border: 1px solid #d0d1d7;
}

/*** 3.3 - CONTACT SUPPORT BLOCK ***/
.sby-support-contact-block {
  background: #ffffff;
  box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.05), 0px 4px 5px rgba(0, 0, 0, 0.05);
  border-radius: 2px;
  padding: 20px 20px 23px;
  margin-top: 28px;
  box-sizing: border-box;
  margin-bottom: 12px;
  display: flex;
}
.sby-support-contact-block .sb-cb-content h3 {
  font-size: 24px;
  line-height: 29px;
  color: #141b38;
  margin: 0;
  margin-bottom: 16px;
}
.sby-support-contact-block .sb-cb-icon {
  margin-right: 30px;
}
.sby-support-contact-block .sb-cb-icon span {
  background: #e8e8eb;
  border-radius: 60px;
  width: 68px;
  height: 68px;
  display: inline-block;
  text-align: center;
  padding-top: 22px;
  box-sizing: border-box;
}
.sby-support-contact-block .sb-cb-icon span svg {
  margin-left: auto;
  margin-right: auto;
}
.sby-support-contact-block .sb-cb-btn {
  background: #fe544f;
  border-radius: 2px;
  color: #fff;
  text-decoration: none;
  display: inline-block;
  padding: 8px 12px;
  font-size: 14px;
}
.sby-support-contact-block .sb-cb-btn:hover {
  background: #ec352f;
  border-color: #ec352f;
}

.sby-support-contact-block .sb-cb-btn:focus,
.sby-support-contact-block .sb-cb-btn:active {
  background: #bc120e;
  border-color: #bc120e;
  outline: none;
  box-shadow: none;
}
.sby-support-contact-block .sb-cb-btn span {
  margin-left: 11px;
}
.sby-support-contact-block .sb-cb-btn svg {
  width: 5px;
}
.sby-support-contact-block .sb-cb-btn path {
  fill: #fff;
}
.sby-support-contact-block .sb-contact-block-left {
  flex-basis: 625px;
  /* width: 625px; */
  /* float: left; */
  display: flex;
  padding-top: 10px;
  box-sizing: border-box;
}
.sby-support-contact-block .sb-contact-block-right {
  flex-basis: 220px;
  /* width: 220px;
    float: left; */
  padding-top: 10px;
  padding-left: 32px;
  box-sizing: border-box;
  border-left: 1px solid #dcdde1;
}
.sby-support-contact-block .sb-contact-block-right p {
  font-size: 12px;
  line-height: 18px;
  color: #141b38;
}
.sby-support-contact-block .sb-contact-block-right img {
  max-width: 65px;
}

/*** 3.4 - SYSTEM INFO BLOCK ***/
.sby-system-info-section {
  background: #ffffff;
  box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.05), 0px 4px 5px rgba(0, 0, 0, 0.05);
  border-radius: 2px;
}
.sby-system-info-section .sby-system-header {
  display: flex;
  padding: 12px 20px 0;
  justify-content: space-between;
}
.sby-system-info-section .sby-system-header h3 {
  font-weight: 600;
  font-size: 18px;
  line-height: 25px;
  color: #141b38;
  margin-top: 9px;
}
.sby-system-info-section .sby-system-header .sby-copy-btn {
  background: #f3f4f5;
  border: 1px solid #dcdde1;
  box-sizing: border-box;
  border-radius: 2px;
  font-size: 14px;
  font-weight: 600;
  line-height: 22px;
  color: #141b38;
  height: 38px;
  padding: 5px 20px 5px 12px;
  cursor: pointer;
  transition: all 0.15s ease-in-out;
}
.sby-system-info-section .sby-system-header .sby-copy-btn:hover {
  background: #ffffff;
  border: 1px solid #d0d1d7;
}
.sby-system-info-section .sby-system-header .sby-copy-btn:focus,
.sby-system-info-section .sby-system-header .sby-copy-btn:active {
  background: #e8e8eb;
  border: 1px solid #d0d1d7;
}
.sby-system-info-section .sby-system-header .sby-copy-btn svg {
  height: 19px;
  width: 19px;
  transform: translate(0px, 4px);
  margin-right: 5px;
}
.sby-system-info-section .sby-system-info {
  padding: 0 20px 20px;
  border-bottom: 1px solid #e8e8eb;
}
.sby-system-info-section .sby-system-info .system_info:focus {
  outline: none;
  box-shadow: none;
}
.sby-system-info-section .sby-system-info .system_info a {
  color: #0068a0;
}
.sby-system-info-section .sby-system-info .system_info {
  box-sizing: border-box;
  background: #f9f9fa;
  border: 1px solid #e8e8eb;
  width: 100%;
  resize: none;
  border-radius: 0;
  padding: 20px 28px;
  font-size: 12px;
  line-height: 18px;
  color: #141b38;
  height: 123px;
  font-family: "Fira Code", monospace;
  word-break: break-all;
}
.sby-system-info-section .sby-system-info .system_info.expanded {
  height: 600px;
  overflow-x: hidden;
  overflow-y: scroll;
}
.sby-system-info-section .sby-system-info .system_info.collapsed {
  overflow: hidden;
}
.sby-system-info-section .sby-system-info .sby-expand-btn {
  padding: 8px 12px 8px 8px;
  background: #ffffff;
  border: 1px solid #d0d1d7;
  border-radius: 2px;
  font-size: 12px;
  line-height: 19px;
  color: #141b38;
  width: 100%;
  margin-top: -5px;
  z-index: 9;
  position: relative;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.15s ease-in-out;
}
.sby-system-info-section .sby-system-info .sby-expand-btn:hover {
  background: #f3f4f5;
  border: 1px solid #dcdde1;
}
.sby-system-info-section .sby-system-info .sby-expand-btn span {
  margin-right: 10px;
}
.sby-system-info-section .sby-system-info .sby-expand-btn span svg {
  transform: translate(0px, -1px);
}
.sby-system-info-section .system_info.expanded + .sby-expand-btn svg {
  transform: rotate(180deg) translate(0px, 1px);
}
.sby-system-info-section .sby-system-info .sby-expand-btn:focus,
.sby-system-info-section .sby-system-info .sby-expand-btn:active {
  background: #e8e8eb !important;
  color: #141b38 !important;
  border: 1px solid #d0d1d7 !important;
}
.sby-export-settings-section {
  box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.05), 0px 4px 5px rgba(0, 0, 0, 0.05);
  border-radius: 2px;
  padding: 20px;
  background: #ffffff;
  display: flex;
  justify-content: space-between;
}
.sby-export-settings-section .sby-export-right {
  display: flex;
}
.sby-export-settings-section .sby-export-left h3 {
  font-style: normal;
  font-weight: 600;
  font-size: 18px;
  line-height: 25px;
  color: #141b38;
  margin: 0 0 4px 0;
}
.sby-export-settings-section .sby-export-left p {
  font-size: 12px;
  line-height: 18px;
  color: #141b38;
  margin: 0;
}
.sby-export-settings-section .sby-select {
  min-width: 234px;
  border: 1px solid #d0d1d7;
  padding: 3px 15px;
  height: 38px;
  font-size: 14px;
  color: #141b38;
  -webkit-appearance: none;
  appearance: none;
  margin-right: 8px;
  background: #fff
    url("data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAoAAAAFCAYAAAB8ZH1oAAAACXBIWXMAAAsTAAALEwEAmpwYAAAAAXNSR0IArs4c6QAAAARnQU1BAACxjwv8YQUAAABSSURBVHgBfc4hCkAhDAbgjV3slQcvvHuYDWY1G8zewyIWb6YuCCLqD4Pt5wtD54NBQA2XVKiWcorl/X7s+DkhJYUhPk54IN5plCue0Tb8M8/aNx/uIpRVqbbFAAAAAElFTkSuQmCC")
    no-repeat right 15px top 55%;
}
.sby-export-settings-section .sby-btn {
  height: 38px;
  font-size: 14px;
  padding: 7px 20px 7px 16px;
  display: flex;
  align-items: center;
  vertical-align: middle;
  background: #f3f4f5;
  border: 1px solid #dcdde1;
  border-radius: 2px;
  font-weight: 500;
  transition: all 0.15s ease-in-out;
  cursor: pointer;
}
.sby-export-settings-section .sby-btn:disabled {
  cursor: not-allowed;
