.sby-support-contact-block {
}

/*** 4.0 Sticky Widget ***/
.sby-stck-wdg {
  position: fixed;
  right: 21px;
  z-index: 9;
  bottom: 20px;
}
.sby-stck-wdg-btn {
  width: 52px;
  height: 52px;
  background: #fff;
  border-radius: 50%;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  filter: drop-shadow(0px 9px 13px rgba(0, 0, 0, 0.2));
}
.sby-stck-wdg-btn svg {
  width: 25px;
  fill: #fe544f;
  height: 33px;
  float: left;
}

.sby-stck-wdg[data-active="true"] .sby-stck-wdg-btn-cls,
.sby-stck-wdg[data-active="true"] .sby-stck-wdg-btn-cls svg {
  display: block;
}
.sby-stck-wdg-btn-cls {
  width: inherit;
  height: inherit;
  position: relative;
  color: #364152;
  box-shadow: 0px 1px 6px rgb(0 0 0 / 5%), 0px 9px 12px rgb(0 0 0 / 5%);
  border-radius: 70px;
}
.sby-stck-wdg-btn-cls svg {
  width: 14px;
  height: 14px;
  position: absolute;
  top: 50%;
  right: 0;
  bottom: 0;
  left: 50%;
  margin-top: -7px;
  margin-left: -7px;
}

.sby-stck-pop {
  position: absolute;
  width: 292px;
  height: auto;
  background: #fff;
  border: 1px solid #e2e8f0;
  box-sizing: border-box;
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.15), 0px 3px 14px rgba(0, 0, 0, 0.12);
  border-radius: 2px;
  padding: 20px;
  right: 0px;
  bottom: 66px;
  color: #141b38;
  padding-bottom: 82px;
}
.sby-stck-wdg[data-active="true"] .sby-stck-pop {
  bottom: 66px;
  opacity: 1;
  visibility: visible;
}

.sby-stck-pop svg {
  fill: currentColor;
}
.sby-stck-el-list {
  border: 1px solid #dcdde1;
  border-radius: 2px;
}
.sby-stck-el {
  display: flex;
  align-items: center;
  padding: 11px 13px;
  border-bottom: 1px solid #dcdde1;
  transition: background 0.15s ease-in-out;
  font-size: 12px;
}
.sby-stck-el:hover {
  background: #f3f4f5;
}
